
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

/* Target the connection-dialog modal container - try multiple selectors */
.vm--modal[data-modal="connection-dialog"],
.vm--modal[name="connection-dialog"],
.vm--container .vm--modal {
    background-color: var(--bg) !important;
    border: 2px solid var(--lighter-primary) !important;
    color: var(--text-primary) !important;
}

/* Ensure overlay has proper opacity */
.vm--overlay {
    background: rgba(0, 0, 0, var(--modal-overlay-opacity)) !important;
}
