
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.message {
  position: relative;
  padding: 8px 16px;
  margin-bottom: 10px;
  border-radius: 8px;
  display: flex;
  align-items: center;

  &-content {
    flex: 1;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 20px;
    
    &-inner {
      width: 100%;
      font-size: 14px;
      line-height: 1.4;
    }
    
    :deep(a) {
      text-decoration: underline;
      cursor: pointer;
    }
    
    :deep(p) {
      margin: 0;
    }
  }

  &-info {
    background-color: var(--success-bg, #e6f7ef);
    color: var(--success-text, #1d7d5c);
    border: 1px solid var(--success-border, #a9e2cd);

    :deep(a) {
      color: var(--success-text, #1d7d5c);
    }
  }

  &-notice {
    background-color: var(--warning-bg, #fff8e6);
    color: var(--warning-text, #b86e00);
    border: 1px solid var(--warning-border, #ffd685);

    :deep(a) {
      color: var(--warning-text, #b86e00);
    }
  }

  &-warning {
    background-color: var(--error-bg, #feecec);
    color: var(--error-text, #cd3a3a);
    border: 1px solid var(--error-border, #f6baba);

    :deep(a) {
      color: var(--error-text, #cd3a3a);
    }
  }
}

.hide-button {
  background: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
  margin-left: 10px;
  padding: 0 5px;
  line-height: 1;
  opacity: 0.6;
  transition: opacity 0.2s;

  &:hover {
    opacity: 1;
  }
}
